/* Setel dasar HTML dan body */
html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    font-family: 'Lato', sans-serif;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    padding-top: -50px; /* Sesuaikan dengan tinggi navbar */
}

/* Gaya untuk skip-link */
.skip-link {
    position: absolute;
    top: -40px;
    left: 0;
    background: rgba(33, 130, 132, 0.8);
    color: white;
    padding: 8px 16px;
    z-index: 100;
    transition: top 0.3s;
    text-decoration: none;
}

.skip-link:focus {
    top: 10px;
}

/* Gaya untuk navigasi */
/* Gaya untuk navbar */
.nav {
    background-color: #F0A04B;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    padding: 15px 20px; /* Tambah padding agar lebih lega */
    font-size: 16px;
    
    position: fixed;
    top: 0;
    width: 100%;
    height: 70px; /* Sesuaikan tinggi navbar */
    
