}

  
.navbar-menu {
    display: flex; 
    justify-content: center; 
    list-style: none;
    padding: 0;
}

.navbar-menu li {
    padding: 0 15px;
}


.navbar-menu a {
    text-decoration: none;
    color: white;
    font-size: 25px; 
}

.navbar-menu a:hover {
    color: #c7c7c7;
}

.logo {
    display: block; /* Agar bisa diklik seluruh area */
    width: 80px;
    height: 80px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}


.logo:hover {
    opacity: 0.8; /* Efek transparan saat di-hover */
}

/* Gaya untuk menu icon */
#menuIcon {
    display: none; 
    background-color: transparent; 
    border: none;  
    color: white;  
    font-size: 24px;  
    cursor: pointer;  
    padding: 10px;  
}

.navbar-menu.open {
    margin-top: 50px;
}

button, .navbar-menu a, input[type="text"], textarea {
    min-width: 44px;  
    min-height: 44px;
