    font-size: 16px;  
    display: flex;
    align-items: center;
    justify-content: center;
}

#menuIcon {
    width: 44px;
    height: 44px;
}

/* Gaya untuk hero section */
.hero {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    min-height: calc(100vh - 70px); /* Sesuaikan agar tidak tertutup navbar */
    
    text-align: center;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
