/* CSS Grid for cultural list and recommendation cards */
.umkm-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    padding: 1rem;
    font-size: 16px;
  }
  
  /* Styling for cultural cards */
  .umkm-card {
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.2s;
  }
  
  .umkm-card:hover {
    transform: scale(1.05);
  }
  
  .umkm-card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .umkm-card .card-content {
    padding: 1rem;
  }
  
  .umkm-card .card-content p {
    text-align: justify;
  }
  .umkm-card h2 {
    margin: 0 0 0.5rem 0;
    font-size: 1.5rem;
    color: #F0A04B;
  }
  
  .umkm-card p {
    margin: 0.5rem 0;
    color: black;
    font-weight: 500;
  }
  
  .card-item .detail-link {
    color: white;
    text-decoration: none;
    margin-bottom: 10px;
}

.card-item .detail-link:hover {
    text-decoration: underline;
}

.card-item:hover {
    transition: background-color 0.3s ease; 
}

.card-content .detail-link {
