}

.group:hover {
    transform: translateY(-5px); 
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.25);
}

/* FRAME LANDSCAPE */
.photo-frame {
    width: 100%;
    max-width: 600px;
    aspect-ratio: 16 / 9;
    overflow: hidden;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f0f0f0;
    position: relative; /* Untuk efek hover */
}

/* Gambar dalam frame */
.photo-frame .photo-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
}

/* Efek Hover */
.photo-frame:hover .photo-image {
    transform: scale(1.08);
}

/* Border tambahan (opsional) */
.photo-frame::before {
    content: '';
