    margin-top: 20px;
}

.social-box {
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 280px;
    text-align: center;
    transition: transform 0.3s ease;
}

.social-box:hover {
    transform: scale(1.03); /* Efek hover */
}

.social-box h3 {
    margin-bottom: 10px;
    font-size: 1.2em;
}

.social-links {
    display: flex;
    justify-content: center;
    gap: 10px;
}

.social-links a img {
    width: 50px;  /* Sesuaikan ukuran */
    height: 50px; 
    border-radius: 50%;  /* Membuat gambar menjadi bulat */
    border: 2px solid #ddd;  /* Opsional: Tambahkan border */
    padding: 5px;  /* Opsional: Beri jarak dalam */
    background-color: white; /* Latar belakang putih */
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.social-links a img:hover {
    transform: scale(1.1);  /* Efek zoom saat hover */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);  /* Efek bayangan */
}

/* Responsive: jika layar kecil, kotak menjadi bertumpuk */
@media (max-width: 600px) {
    .social-container {
        flex-direction: column;
        align-items: center;
    }
}

.loading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(255, 255, 255, 0.8);
