    border: 8px solid rgba(0, 0, 0, 0.1);
    border-top: 8px solid #F0A04B;
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
  .loading-text {
    margin-top: 10px;
    font-size: 18px;
    color: #333;
  }
  
  .search-container {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Lato', sans-serif;
    margin-top: 30px;
    margin-bottom: -10px;
  }
  
  #search-input {
    width: 800px;
    padding: 8px;
    border: 1px solid transparent;
    border-radius: 4px;
    margin-right: 8px;
    box-shadow: 0 0 0 2px #F0A04B; 
    font-family: 'Lato', sans-serif;
    outline: none; 
    font-weight: 500;
  }
  
  #search-button {
    padding: 8px 16px;
    background-color: #F0A04B;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  #search-button:hover {
background-color: #F0A04B; 
  transition: background-color 0.3s ease; 
  }

  .umkm-info h2 {
    color: #F0A04B;
    font-size: 40px;
  }
  
.info h2 {
    font-size: 30px;
    color: #F0A04B;
    padding-bottom: 5px;
    align-items: center;
    margin-top: 5px;
}

.no-results {
    font-size: 1.2em;
    color: #F0A04B; 
    text-align: center;
    margin-top: 10px;
    font-weight: bold;
  }
 
  
  /* Gaya untuk location container */
.location-container {
    width: 90%;
    max-width: 1000px;
    margin: 20px auto;
    padding: 20px;
    background-color: #ffffff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    text-align: center;
    border-radius: 10px;
    display: block;
    visibility: visible;
}

.location-container h2 {
    margin-top: 5px;
    color: #F0A04B;
}

.alamat {
    color: #F0A04B;
    margin-bottom: 5px;
}

/* Gaya untuk map-container */
.map-container {
    width: 100%;
    height: 400px;
    margin-top: 15px;
    border-radius: 8px;
}

.map-container iframe {
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 8px;
}
  
/* Gaya untuk footer */
footer {
    background-color: #F0A04B;
    color: #ffffff;
    text-align: center;
    padding: 10px 10px;
    font-size: 16px;
