er-radius: 4px;\r\n    cursor: pointer;\r\n  }\r\n  \r\n  #search-button:hover {\r\nbackground-color: #F0A04B; \r\n  transition: background-color 0.3s ease; \r\n  }\r\n\r\n  .umkm-info h2 {\r\n    color: #F0A04B;\r\n    font-size: 40px;\r\n  }\r\n  \r\n.info h2 {\r\n    font-size: 30px;\r\n    color: #F0A04B;\r\n    padding-bottom: 5px;\r\n    align-items: center;\r\n    margin-top: 5px;\r\n}\r\n\r\n.no-results {\r\n    font-size: 1.2em;\r\n    color: #F0A04B; \r\n    text-align: center;\r\n    margin-top: 10px;\r\n    font-weight: bold;\r\n  }\r\n \r\n  \r\n  /* Gaya untuk location container */\r\n.location-container {\r\n    width: 90%;\r\n    max-width: 1000px;\r\n    margin: 20px auto;\r\n    padding: 20px;\r\n    background-color: #ffffff;\r\n    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\r\n    text-align: center;\r\n    border-radius: 10px;\r\n    display: block;\r\n    visibility: visible;\r\n}\r\n\r\n.location-container h2 {\r\n    margin-top: 5px;\r\n    color: #F0A04B;\r\n}\r\n\r\n.alamat {\r\n    color: #F0A04B;\r\n    margin-bottom: 5px;\r\n}\r\n\r\n/* Gaya untuk map-container */\r\n.map-container {\r\n    width: 100%;\r\n    height: 400px;\r\n    margin-top: 15px;\r\n    border-radius: 8px;\r\n}\r\n\r\n.map-container iframe {\r\n    width: 100%;\r\n    height: 100%;\r\n    border: none;\r\n    border-radius: 8px;\r\n}\r\n  \r\n/* Gaya untuk footer */\r\nfooter {\r\n    background-color: #F0A04B;\r\n    color: #ffffff;\r\n    text-align: center;\r\n    padding: 10px 10px;\r\n    font-size: 16px;\r\n}\r\n"],"sourceRoot":""}]);
